.button {
  display: inline-flex;
  background-color: var(--color-white);
  border-color: var(--color-contrast-low);
  color: var(--color-text);
  font-size: var(--button-font-size);
  line-height: 1;
  cursor: pointer;
  padding: var(--space-xs) var(--space-sm);
  text-align: center;
  white-space: nowrap;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  @extend %unselectable;
  @include transition(color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out);

  &:hover {
    border-color: var(--color-contrast-medium);
    color: var(--color-text);
  }
  &:focus {
    border-color: var(--color-contrast-medium);
    color: var(--color-text);
    //&:not(:active) {
      //box-shadow: 0 0 0 3px var(--color-contrast-a25);
    //}
  }
  &:active {
    border-color: var(--color-text);
    color: var(--color-text);
  }

  // Themes

  &.is-primary,
  &.is-accent {
    background-color: var(--button-color);
    border-color: transparent;
    color: var(--color-white);
    &:hover {
      background-color: var(--button-color-hover);
      border-color: transparent;
      color: var(--color-white);
    }
    &:focus {
      border-color: transparent;
      color: var(--color-white);
      &:not(:active) {
        box-shadow: 0 0 0 3px var(--button-shadow);
      }
    }
    &:active {
      background-color: var(--button-color-active);
      border-color: transparent;
      color: var(--color-white);
    }
    &[disabled] {
      background-color: var(--button-color);
      border-color: transparent;
      box-shadow: none;
    }

    // Outlined version
    &.is-outlined {
      background-color: transparent;
      border-color: var(--button-color);
      color: var(--button-color);
      &:hover,
      &:focus {
        background-color: var(--button-color);
        border-color: var(--button-color);
        color: var(--color-white);
      }
      &[disabled] {
        background-color: transparent;
        border-color: var(--button-color);
        box-shadow: none;
        color: var(--button-color);
      }
    }
  }

  &.is-primary {
    --button-color: var(--color-primary);
    --button-color-hover: var(--color-primary-dark);
    --button-color-active: var(--color-primary-darker);
    --button-shadow: var(--color-primary-a25);
  }

  &.is-accent {
    --button-color: var(--color-accent);
    --button-color-hover: var(--color-accent-dark);
    --button-color-active: var(--color-accent-darker);
    --button-shadow: var(--color-accent-a25);
  }

  // Sizes

  &.is-sm { font-size: var(--button-sm); }
  &.is-md { font-size: var(--button-md); }
  &.is-lg { font-size: var(--button-lg); }

  // Modifiers

  &[disabled] {
    background-color: var(--color-white);
    border-color: var(--color-contrast-low);
    box-shadow: none;
    opacity: 0.5;
  }

  &.is-fullwidth {
    display: flex;
    width: 100%;
  }
}
